import React, { useEffect, useState } from "react";
import { styled } from "@mui/material/styles";
import { Link } from "react-router-dom";
import "src/style/Partyroom/Partyroom.css";

import table from "src/assets/images/partyroom/table.png";
import chair from "src/assets/images/partyroom/chair.png";
import previous from "src/assets/images/partyroom/previous.png";
import cakestack from "src/assets/images/partyroom/cakestack.png";
import maincharacter from "src/assets/images/partyroom/.png";
import guest1 from "src/assets/images/partyroom/.png";
import guest2 from "src/assets/images/partyroom/.png";
import guest3 from "src/assets/images/partyroom/.png";
import guest4 from "src/assets/images/partyroom/guest4.png";

function Partyroom() {

  const [daysRemaining, setDaysRemaining] = useState(0);

  useEffect(() => {
    // D-day  
    const targetDate = new Date("2023-08-17"); //   
    const today = new Date();
    const timeDifference = targetDate - today;
    const remainingDays = Math.ceil(timeDifference / (1000 * 60 * 60 * 24));

    setDaysRemaining(remainingDays);
  }, []);


  return (
    <div className="Background">
      
      <div className="countdown-wrapper">
        <div className="countdown">{` D-${daysRemaining} day `}</div>
      </div>
      <img src={table} className="table" alt="" />
      <Link to="/AftLogin">
        <img src={previous} className="previous" alt="" />
      </Link>

      <div className="chair-container">
        <img src={chair} className="chair" alt=" " />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        <img src={chair} className="chair" alt="" />
        {/*      */}
      </div>

      <img src={cakestack} className="cakestack1" alt=" " />
      <img src={cakestack} className="cakestack2" alt=" " />
      <img src={cakestack} className="cakestack3" alt=" " />
      <img src={cakestack} className="cakestack4" alt=" " />

      <img src={maincharacter} className="maincharacter" alt=" " />
      <img src={guest1} className="guest1" alt="" />
      <img src={guest2} className="guest2" alt="" />
      <img src={guest3} className="guest3" alt="" />
      <img src={guest4} className="guest4" alt="" />
    </div>
  );
}

export default Partyroom;