import React, { useEffect, useState } from "react";
import "src/style/Join/join.css";
import CreateJoin from "src/assets/images/join/Join.png";
import GotoLogin from "src/assets/images/join/Login.png";
import { Link } from "react-router-dom";

const User = {
  email: "likelion@naver.com",

  pw: "likelion123",

  birth: "0101",
};

export default function Join() {
  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");
  const [checkPw, setCheckPw] = useState("");
  const [birth, setBirth] = useState("");

  const [emailValid, setEmailValid] = useState(false);
  const [pwValid, setPwValid] = useState(false);
  const [checkPwValid, setCheckPwValid] = useState(false);
  const [birthValid, setBirthValid] = useState(false);
  const [notAllow, setNotAllow] = useState(true);

  const handleEmail = (em) => {
    setEmail(em.target.value);
    const regex =
      /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
    if (regex.test(email)) {
      setEmailValid(true);
    } else {
      setEmailValid(false);
    }
  };

  const handlePassword = (p) => {
    setPw(p.target.value);
    const regex = /^[A-Za-z0-9]{6,20}$/;
    if (regex.test(pw)) {
      setPwValid(true);
    } else {
      setPwValid(false);
    }
  };

  const handleCheckPw = (cp) => {
    setCheckPw(cp.target.value);
    const regex = /^[A-Za-z0-9]{6,20}$/;
    if (regex.test(cp)) {
      setCheckPwValid(true);
    } else {
      setCheckPwValid(false);
    }
  };

  const handleBirth = (i) => {
    setBirth(i.target.value);
    if (birth.length === 3) {
      setBirthValid(true);
    } else {
      setBirthValid(false);
    }
  };

  const onClickConfirmButton = () => {
    if (
      email === User.email &&
      pw === User.pw &&
      checkPw === pw &&
      birth === User.birth
    ) {
      alert(" !");
    } else {
      alert(" !");
    }
  };

  useEffect(() => {
    if (emailValid && pwValid && checkPwValid) {
      setNotAllow(false);
      return;
    }
    setNotAllow(true);
  }, [emailValid, pwValid, checkPwValid]);

  return (
    <div className="wrapper">
      <div className="background">
        <p className="Jointitle"> </p>
        <h2 className="ID">ID</h2>
        <input
          className="Loginbox"
          placeholder="     !"
          value={email}
          onChange={handleEmail}
        />
        <div className="errorID">
          {!emailValid && email.length > 0 && (
            <div>  !</div>
          )}
        </div>

        <h2 className="PW">PW</h2>
        <input
          type="password"
          className="PwBox"
          placeholder="    !"
          value={pw}
          onChange={handlePassword}
        />
        <div className="errorPW">
          {!pwValid && pw.length > 0 && (
            <div>  6~12 !</div>
          )}
        </div>
        <input
          type="password"
          className="PwCheck"
          placeholder="    !"
          value={checkPw}
          onChange={handleCheckPw}
        />
        <div className="errorCheckPW">
          {!(checkPw === pw) && checkPw.length > 0 && (
            <div>  !</div>
          )}
        </div>
        <h2 className="Birth">Birth</h2>
        <input
          className="BirthBox"
          placeholder="    !"
          value={birth}
          onChange={handleBirth}
        />
        <div className="errorCheckBirth">
          {!birthValid && birth.length > 0 && (
            <div> 4 ! </div>
          )}
        </div>

        <p className="FinishedJoin">
          {checkPw === pw && checkPw.length > 0 && birth.length === 4 && (
            <div> !</div>
          )}
        </p>

        <img
          onClick={onClickConfirmButton}
          disabled={notAllow}
          src={CreateJoin}
          className="CreateJoin"
          alt=""
        />

        <Link to="/Login">
          <img src={GotoLogin} className="GotoLogin" alt="" />
        </Link>
      </div>
    </div>
  );
}
