import React, { useEffect, useState } from "react";
import loginHead from "src/assets/images/loginHead.png";
import LoginButton from "src/assets/images/join/Login.png";
import JoinButton from "src/assets/images/join/Join.png";
import "src/style/Login/Login.css";
import { Link } from "react-router-dom";

//  
const User = {
  email: "likelion@naver.com",
  pw: "likelion123",
  birth: "0101",
};

function Login() {
  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");

  const [emailValid, setEmailValid] = useState(false);
  const [pwValid, setPwValid] = useState(false);
  const [notAllow, setNotAllow] = useState(true);

  const handleEmail = (e) => {
    setEmail(e.target.value);
    const regex =
      /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;
    if (regex.test(email)) {
      setEmailValid(true);
    } else {
      setEmailValid(false);
    }
  };

  const handlePassword = (p) => {
    setPw(p.target.value);
    const regex = /^[A-Za-z0-9]{6,20}$/;
    if (regex.test(pw)) {
      setPwValid(true);
    } else {
      setPwValid(false);
    }
  };

  const onClickConfirmButton = () => {
    if (email === User.email && pw === User.pw) {
      alert(" !");
    } else {
      alert("  .");
    }
  };

  useEffect(() => {
    if (emailValid && pwValid) {
      setNotAllow(false);
      return;
    }
    setNotAllow(true);
  }, [emailValid, pwValid]);

  return (
    <div className="wrapper">
      <div className="background">
        <img src={loginHead} className="loginHead" alt=""></img>

        <p className="loginID">ID</p>
        <input
          type="text"
          className="loginbox"
          placeholder="     !"
          value={email}
          onChange={handleEmail}
        />
        <div className="loginErrorID">
          {!emailValid && email.length > 0 && (
            <div>  !</div>
          )}
        </div>
        {/*    
    6~12   */}

        <p className="loginPW">PW</p>
        <input
          type="password"
          className="loginPwBox"
          placeholder="    !"
          value={pw}
          onChange={handlePassword}
        />
        <div className="loginErrorPW">
          {!pwValid && pw.length > 0 && (
            <div>   6~12 !</div>
          )}
        </div>
        <div>
          <Link to="/AvatarStart">
            <img
              onClick={onClickConfirmButton}
              disabled={notAllow}
              src={LoginButton}
              className="LoginButton"
              alt="LoginButton"
            />
          </Link>
          <p className="askMemberYet">  ?</p>

          <Link to="/Join">
            <img
              src={JoinButton}
              className="JoinButton"
              alt=" "
            />
          </Link>
        </div>
      </div>
    </div>
  );
}

export default Login;
